import React from "react";
import { slide as Menu } from 'react-burger-menu';

export default props => {
  return (
    <Menu {...props}>
         <aside className="sidebar">
        <div className="top-area">
          <header className="profile-head">
            <div className="image-holder">
              <img className="img-thumbnail rounded-circle thumb64" style = {{width : "100px"}} src="https://pixinvent.com/materialize-material-design-admin-template/app-assets/images/user/12.jpg" alt="user" />
            </div>
            <div className="text-holder">
              <strong className="name">Jessica Matthews <a href="/user" className="setting"><i className="far fa-cog" /></a></strong>
              <a href="/points" className="points"><span className="green">1252</span> Points <i className="far fa-chevron-right" /></a>
            </div>
          </header>
          <ul className="list-menu">
            <li><a href="/dashboard"><i className="fas fa-tachometer" />Dashboard</a></li>
            <li><a href="/map"><i className="fas fa-globe-americas" />News &amp; Map</a></li>
            <li><a href="/areas"><i className="fas fa-map-pin" />Risk Areas</a></li>
            <li><a href="/health"><i className="fas fa-book-medical" />Health 2.0</a></li>
            <li><a href="/avoidCrowds"><i className="fas fa-users" />Avoid Crowds</a></li>
          </ul>
          <div className="more-links">
            <span className="heading">Coming soon</span>
            <ul className="list-menu disables">
              <li><a href="/map"><i className="far fa-globe-europe" />Global Map</a></li>
            </ul>
          </div>
        </div>
        <div className="bottom-area">
          <ul className="list-menu">
            <li><a href="/more" className="active"><i className="fas fa-lock-alt" />Unlock more features</a></li>
            <li><a href="/how"><i className="fas fa-question-circle" />How does it work?</a></li>
            <li><a href="#" onClick={()=>{localStorage.clear('token') &&
   props.history.push("/landing")}}><i className="far fa-sign-out-alt" />Sign Out</a></li>
          </ul>
          <strong className="logo"><a href="/"><img src="https://stealthify.io/wp-content/uploads/2020/04/Stealth.ify-Logo-Dark-v1-cropped-300x86.png" style={{width:"150px"}} alt=" description" /></a></strong>
        </div>
      </aside>
    </Menu>
  );
};

