import React, { Component } from 'react'; 
import {Map, InfoWindow, Marker, GoogleApiWrapper} from 'google-maps-react';
import SideBar from "../sidebar";
import { getDistance ,getPlaces  } from "../../services/logInService";
import "./Map.css";
 
export class MapContainer extends Component {


  constructor(props) {
    super(props);
    this.state = {
      registerForm: {
        lat: "",
        lng:"",
        distance:""
      },
    };
  }



  componentWillMount(){

    if(!localStorage.getItem('token')){
      this.props.history.push("/");
    }
  }

 
 componentDidMount() {
this.getDistanceData();
//this.getPlacesData();
  }

  getDistanceData=async ()=>{

    const location = window.navigator && window.navigator.geolocation;
    console.log(location)
    if (location) {
      await location.getCurrentPosition(async(position) => {
        await this.setState({
          lat: position.coords.latitude,
          lng: position.coords.longitude,
        })
        getDistance({lat:this.state.lat,lng:this.state.lng}).then(this.onDistanceSuccess).catch(this.onDistanceError);
        getPlaces({lat:this.state.lat,lng:this.state.lng}).then(this.onPlacesSuccess).catch(this.onPlacesError);

      }, (error) => {
        this.setState({ lat: 'err-latitude', lng: 'err-longitude' })
      })
    }
   
  }

  onPlacesSuccess=(res)=>{
console.log(res)
  }
  

  onDistanceSuccess = (res) => {
   this.setState({distance:res.distance});
  };


  onDistanceError = (res) => {
    console.log(res)
  };

  render() {
    return (
      <React.Fragment>
        {this.state.distance?
      <h1>
     You are  {this.state.distance} away from COVID-19 infected person
              </h1>:<h1>loading....</h1>}
      
      <SideBar className="sideMenu_class" pageWrapId={"page-wrap"} outerContainerId={"App"} />


      <div className= "container_map">
      <Map google={this.props.google} style={{ width: "88%"}} zoom={14}>
 
        <Marker onClick={this.onMarkerClick}
                name={'Current location'} />
 
        <InfoWindow onClose={this.onInfoWindowClose}>
            <div>
            </div>
        </InfoWindow>
      </Map>
      </div>
      </React.Fragment>
    );
  }
}
 
export default GoogleApiWrapper({
  // apiKey: "AIzaSyASUgxNOHFzT1QVWeL-2YVAu4kYTxnaTco"
})(MapContainer)
